    const token = localStorage.getItem("jwtToken"); // ä»ŽlocalStorageèŽ·å–JWTä»¤ç‰Œ
    if (!token) {
      router.replace("/login");
    }
    const headers = {
      Authorization: `Bearer ${token}`
    };
    const fetchLoginToken = () => {
      axios.post("/api/loginToken?token=" + token).then(response => {
        if (response.data.code == 0) {
          console.error(response.data.data);
          router.replace("/login");
          return;
        }
        console.log(response.data.data);
        const decodedToken = jwtDecode(token);
        // ä»Žè§£ç åŽçš„ä»¤ç‰Œä¸­èŽ·å–ç‰¹å®šçš„æ•°æ®
        userId.value = decodedToken.id; // ä»Žä»¤ç‰Œä¸­èŽ·å–ç”¨æˆ·ID
        // å¦‚æžœéœ€è¦æ‰§è¡Œä¸€äº›ç‰¹å®šçš„æ“ä½œï¼Œå¯ä»¥åœ¨è¿™é‡Œæ·»åŠ ä»£ç 
        fetchDataAndFillForm();
      }).catch(error => {
        console.error("è¯·æ±‚loginTokenæŽ¥å£å¤±è´¥", error);
        router.replace("/login");
      });
    };
    const fetchDataAndFillForm = async () => {
      try {
        const response = await axios.get(`/api/selectUserAllRecord?id=${userId.value}`, {
          headers
        });
        data.value = response.data.data; // å‡è®¾æœåŠ¡å™¨è¿”å›žçš„æ•°æ®æ˜¯ä¸€ä¸ªåŒ…å«ä¸Šè¿°å­—æ®µçš„å¯¹è±¡
        axios.get(`/api/selectUser?id=${userId.value}`, {
          headers
        }).then(response => {
          userData.value = response.data.data; // å‡è®¾æœåŠ¡å™¨è¿”å›žçš„æ•°æ®æ˜¯ä¸€ä¸ªåŒ…å«ä¸Šè¿°å­—æ®µçš„å¯¹è±¡
          console.log(userData.value);
          axios.get(`/api/generateQRCode?text=${userData.value.displayUrl}`, {
            headers
          }).then(response => {
            console.log(userData.value.displayUrl);
            imageUrl.value = response.data.data; // å‡è®¾æœåŠ¡å™¨è¿”å›žçš„æ•°æ®æ˜¯ä¸€ä¸ªåŒ…å«ä¸Šè¿°å­—æ®µçš„å¯¹è±¡
